'use client'
import axios from "axios";
import { useEffect, useState } from "react";
import {Input} from "@/components/ui/input"

interface Weather {
    city: string;
    current: {
        temperature: number;
        feels_like: number;
        humidity: number;
        pressure: number;
        wind_speed: number;
        visibility: number;
        description: string;
        icon: string;
    };
}

export default function Dashboard() {
    const WEATHER_API = "http://localhost:5000/get-weather?city=Bulacan";

    const [weather, setWeather] = useState<Weather | null>(null);

    const getWeather = async () => {
        try {
            const response = await axios.get(WEATHER_API);
            setWeather(response.data);
        } catch (error) {
            console.error("Error fetching weather data:", error);
        }
    };

    useEffect(() => {
        getWeather();
    }, []);

    return (
        <div>
            {weather ? (
                <div className="flex h-screen items-center justify-center">
                    <div className="border w-72 h-80">
                        <div>

                        </div>
                        
   
                    </div>
                    <div>
                    <p>Feels Like: {weather.current.feels_like}Â°C</p>
                    <p>Humidity: {weather.current.humidity}%</p>
                    <p>Wind Speed: {weather.current.wind_speed} m/s</p>
                    <p>Description: {weather.current.description}</p>
                    </div>

 


                    
                </div>
            ) : (
                <pre>{JSON.stringify(weather, null, 2)}</pre>
            )}
        </div>
    );
}
